<template>
  <div id="staffs">
    <Header>
      <el-breadcrumb separator-class="el-icon-arrow-right" slot="left">
        <el-breadcrumb-item>流程管理</el-breadcrumb-item>
        <el-breadcrumb-item>出差流程</el-breadcrumb-item>
      </el-breadcrumb>
      <div slot="right">
      </div>
    </Header>
    <div class="inner">
      <el-row>
        <el-col :span="8" style="margin-top: 10px;">
          <el-input placeholder="输入关键词查询" v-model="keywords" @change="search">
            <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <el-table :data="processes" border style="width: 100%; margin-top: 20px">
        <el-table-column label="员工" prop="userName" width="180px;">
        </el-table-column>
        <el-table-column label="部门" prop="deptName">
        </el-table-column>
        <el-table-column label="预算部门">
          <template slot-scope="scope" v-if="scope.row.group">
            {{scope.row.group.name}}
          </template>
        </el-table-column>
        <el-table-column label="直接审批人">
          <template slot-scope="scope">
            {{scope.row.managers1.join()}}
          </template>
        </el-table-column>
        <el-table-column label="二级审批人">
          <template slot-scope="scope">
            {{scope.row.managers2.join()}}
          </template>
        </el-table-column>
        <el-table-column width="140px;">
          <template slot="header">
            <el-popover placement="bottom-end" title="流程类型" width="240" trigger="hover"
              content="普通：部门变动自动修改审批流程。 特殊：部门变动不自动修改流程。">
              <div slot="reference">
                <span>流程类型</span>
                <span><i class="el-icon-question"></i></span>
              </div>
            </el-popover>
          </template>
          <template slot-scope="scope">
            {{typeMap[scope.row.type]}}
          </template>
        </el-table-column>
        <el-table-column label="操作" width="120px;">
          <template slot-scope="scope">
            <el-button plain size="small" type="warning" @click="editProcess(scope.row)">修 改</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination :current-page="page" :page-size="limit" layout="total, prev, pager, next, jumper" :total="count"
        align="right" @current-change="changePage" class="pagi">
      </el-pagination>
    </div>

    <el-dialog title="出差审批流修改" :visible.sync="editVisible" width="80%">
      <el-row style="padding-left:40px;">
        <el-col :span="6">
          姓名: {{process.userName}}
        </el-col>
        <el-col :span="6">
          部门: {{process.deptName}}
        </el-col>
        <el-col :span="6">
          预算部门: {{process.group.name}}
        </el-col>
      </el-row>
      <el-form ref="processForm" :model="process" label-width="120px" class="form1">
        <el-form-item label="流程类型">
          <el-select v-model="process.type" placeholder="选择类型" number class="select1">
            <el-option label="普通流程" :value="0"></el-option>
            <el-option label="特殊流程" :value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="直接审批人">
          <el-input placeholder="请选择" class="input-with-select" readonly :value="process.managers1.join()">
            <el-button slot="append" icon="el-icon-search" @click="showManager('managers1')">选择</el-button>
          </el-input>
        </el-form-item>
        <el-form-item label="二级审批人">
          <el-input placeholder="请选择" :value="process.managers2.join()" class="input-with-select" readonly>
            <el-button slot="append" icon="el-icon-search" @click="showManager('managers2')">选择</el-button>
          </el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false" plain>取 消</el-button>
        <el-button type="success" plain @click="setProcess">确 定</el-button>
      </span>

      <el-dialog title="选择审批人" :visible.sync="managerVisible" append-to-body width="80%">
        <h3>{{managerMap[managerType]}}</h3>
        <!-- 审批人列表 -->
        <el-table :data="managers" border style="width: 100%; margin-top: 20px" size="small">
          <el-table-column label="员工" prop="userName" width="180px;">
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button plain size="small" type="danger" @click="deleteStaff(scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 审批人列表 -->

        <!-- 查询审批人列表 -->
        <el-row style="margin-top:20px;">
          <el-col :span="10">
            <el-input placeholder="输入姓名或部门查询" v-model="staffKeywords" @change="searchStaff">
              <el-button slot="append" icon="el-icon-search" @click="searchStaff"></el-button>
            </el-input>
          </el-col>
        </el-row>
        <el-table :data="staffs" border style="width: 100%; margin-top: 20px" size="small">
          <el-table-column label="员工" prop="userName" width="180px;">
          </el-table-column>
          <el-table-column label="部门">
            <template slot-scope="scope">
              {{scope.row.departments[0].deptName}}
            </template>
          </el-table-column>
          <el-table-column label="操作" width="120px;">
            <template slot-scope="scope">
              <el-button plain size="small" type="success" @click="selectStaff(scope.row)" >选择</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination :current-page="staffPage" :page-size="limit" layout="total, prev, pager, next, jumper" :total="staffCount"
        align="right" @current-change="changeStaffPage" class="pagi">
        </el-pagination>
        <!-- 查询审批人列表 -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="managerVisible = false" plain>取 消</el-button>
          <el-button type="success" plain @click="setManager">确 定</el-button>
        </span>
      </el-dialog>
    </el-dialog>
  </div>
</template>

<script>
  import Header from '../../components/Header'

  export default {
    name: 'staffprocess',
    components: {
      Header
    },
    data() {
      return {
        count: 0,
        page: 1,
        limit: 10,
        processes: [],
        keywords: '',
        typeMap: {
          0: '普通',
          1: '特殊'
        },
        process: {
          group: {},
          managers1: [],
          managers2: []
        },
        editVisible: false,
        managerVisible: false,
        managers: [],
        managerType: 'manager1',
        staffKeywords: '',
        staffCount: 0,
        staffPage: 1,
        staffs: [],
        managerMap: {
          'managers1': '直接审批人',
          'managers2': '二级审批人'
        }
      }
    },
    methods: {
      search() {
        this.page = 1;
        this.keywords = this.keywords.trim();
        this.getStaffProcess()
      },
      getStaffProcess() {
        this.$http.get(`/ec/api/processes/staffs?page=${this.page}&limit=${this.limit}&keywords=${this.keywords}`).then(
          res => {
            let resData = res.data;
            if (resData.errcode === 0) {
              this.processes = resData.data.rows || [];
              this.count = resData.data.count || 0;
            }
          })
      },
      changePage(page) {
        this.page = page;
        this.getStaffProcess()
      },
      editProcess(process) {
        this.process = process;
        this.editVisible = true;
      },
      getStaffLists() {
        this.$http.get(`/ec/api/staffs?page=${this.staffPage}&limit=${this.limit}&keywords=${this.staffKeywords}`).then(
          res => {
            let resData = res.data;
            if (resData.errcode === 0) {
              this.staffs = resData.data.rows || [];
              this.staffCount = resData.data.count || 0;
            }
          })
      },
      showManager(type) {
        this.managerType = type;
        if(type === 'managers1' ) {
          let userArray = [];
          let users = this.process.approvals[0].users ||[];
          for(let user of users) {
            userArray.push({
              userId: user.userId,
              userName: user.userName,
            })
          }
          this.managers = userArray;
        } else {
          if(this.process.approvals[1]) {
            let userArray = [];
            let users = this.process.approvals[1].users ||[];
            for(let user of users) {
              userArray.push({
                userId: user.userId,
                userName: user.userName,
              })
            }
            this.managers = userArray;
          } else {
            this.managers = [];
          }
        }
        this.managerVisible = true;
      },
      searchStaff() {
        this.staffPage = 1;
        this.staffKeywords = this.staffKeywords.trim();
        this.getStaffLists();
      },
      changeStaffPage(page) {
        this.staffPage = page;
        this.getStaffLists();
      },
      selectStaff(staff) {
        let index = this.managers.findIndex(item => item.userId === staff.userId)
        if(index > -1) return;
        this.managers.push({
          userId: staff.userId,
          userName: staff.userName,
        });
      },
      deleteStaff(staff) {
        let index = this.managers.findIndex(item => item.userId === staff.userId)
        this.$delete(this.managers, index);
      },
      setManager() {
        if(this.managerType === 'managers1' ) {
          this.process.approvals[0].users = this.managers;
        } else {
          
          if(this.process.approvals[1]) {
            this.process.approvals[1].users = this.managers; 
          } else {
            this.process.approvals.push({
              sequence: 2,
              users: this.managers
            })
          }
        }
        let userNameArray = [];
        for(let user of this.managers) {
          userNameArray.push(user.userName)
        }
        this.process[this.managerType] = userNameArray;
        this.managers = [];
        this.managerVisible = false;
      },

      setProcess() {
         this.$http.put(`/ec/api/staffs/process`, this.process).then(
          res => {
            let resData = res.data;
            if (resData.errcode === 0) {
              this.getStaffProcess();
              this.editVisible = false;
            }
          })
      }
    },
    created() {
      this.getStaffProcess()
      this.getStaffLists()
    }
  }
</script>

<style>
  .inner {
    padding: 0px 20px;
  }

  .select1 {
    width: 100%;
  }

  .form1 {
    margin-top: 20px;
  }
</style>